body {
  background-color: rgba(red, 0.2);
}

.theme-button {
  display: block;
  width: 100%;
  max-width: max-content;
  padding: 6px 24px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  text-decoration: none;
  color: #222222;
  background-color: red;
  border: 1px solid transparent;
  border-radius: 6px;
  cursor: pointer;
}
